import Vue from 'vue';
import Vuetify from 'vuetify'
import 'vuetify/dist/vuetify.min.css'

import colors from 'vuetify/lib/util/colors'

Vue.use(Vuetify);

export default new Vuetify({
  theme: {
    options: {
      customProperties: true
    },
    dark: true,
    themes: {
      dark: {
        primary: '#1262FF',
        secondary: '#111a24',
        error: '#FF576B',
        success: '#00D46A',

        // custom
        background: '#050e18',
        navbar: '#111a24',
        backgroundHeader: '#0e151d',
        backgroundBody: '#111a24',
        selectBackground: '#0a0f14',

        emdxBlue: '#1262FF',
        borderColor: '#202834',
        test: '#E33C50',

        adminButton: '#1d252f',
        backgroundOverlay: '#07121E',
        // Custom Remake
        primaryBack: "#121A24",
        secondaryBack: "#0E151D",
        lightBack: "#162230",

        primaryColor: "#ffffff",
        secondaryColor: "#888C91",
        lightColor: "#293039",
      },
      light: {
        primary: '#1262FF',
        secondary: colors.grey.darken1,
        accent: colors.shades.black,
        error: "#EF5C6D",
        success: "#49BF78",

        background: "#ECEFF0",

        emdxBlue: "#1262FF",

        // Custom Remake
        primaryBack: "#ffffff",
        secondaryBack: "#F5F5F5",
        lightBack: "#F5F5F5",

        primaryColor: "#050E18",
        secondaryColor: "#82878C",
        lightColor: "#293039",
      }
    }
  },
});



