<script>
import { NETWORKS } from "../util/constants/networks";
import { mapState } from "vuex";
import { ABI } from '../util/constants/casinoContract'

export default {
  name: "casino",
  data() {
    return {
      amount: null,
      pending: false,
      winEvent: null,

      // New variables
      sendToAmount: null,
      sendToAddress: null,
      sendFromAmount: 0.000345,
      sendFromAddress: null,
      transactionDone: false,
      showMore: false,
      loadingTable: false,
      transactionDialog: false,
      headers: [
        { text: "Date", value: "timeStamp" },
        {
          text: "Block N°",
          align: "start",
          sortable: false,
          value: "blockNumber",
        },
        { text: "From", value: "from" },
        { text: "To", value: "to" },
        { text: "Value", value: "value" },
        { text: "Gas", value: "gas" },
      ],
      desserts: []
    }
  },
  computed: mapState({
    coinbase: (state) => state.web3.coinbase,
    isCheckingTransaction: (state) => state.web3.isCheckingTransaction,
    ethBalance: (state) => {
      if (state.web3.web3Instance !== null)
        return state.web3.web3Instance().fromWei(state.web3.balance, "ether");
    },
    network: (state) => NETWORKS[state.web3.networkId],
    isInjected: (state) => state.web3.isInjected,
    latestTransactions: (state) => state.web3.latestTransactions
  }),
  methods: {
    activateMetaMask() {
      window.ethereum.enable();
    },
    weiToEth(value){
      return this.$store.state.web3.toWei(value, 'eth')
  },
  transfer(){
    let address = this.sendToAddress
    let getContract = new Promise(function (resolve, reject) {
    let web3 = new Web3(window.web3.currentProvider)
    let casinoContract = web3.eth.contract(ABI)
    let casinoContractInstance = casinoContract.at(address)
    console.log(casinoContract)
    console.log(casinoContractInstance)
    resolve(casinoContractInstance)
    })

   getContract.then(result => {
        this.$store.commit('registerContractInstance', result)
        this.$store.state.contractInstance().bet(
        1,
        {
          gas: 300000,
          value: this.$store.state.web3
            .web3Instance()
            .toWei(this.sendFromAmount, "ether"),
          from: this.$store.state.web3.coinbase,
        },
        (err, result) => {
          if (err) {
            console.log(err);
            this.pending = false;
          } else {
            this.$store.state.web3.isCheckingTransaction = true;
            this.transactionDone = true;
            this.transactionDialog = true;
            console.log("Empezando a transferir");
            this.pending = false;
            let Won = this.$store.state.contractInstance().Won();
            Won.watch((err, result) => {
              if (err) {
                this.pending = false;
                console.log("could not get event Won()");
              } else {
                this.pending = false;
                console.log("Entro todo bien, hay que modificar");
                this.winEvent = result.args;
                this.winEvent._amount = parseInt(result.args._amount, 10);
              }
            });
          }
        },
      );
      }).catch(e => console.log(e))

  
    },
  },
  mounted() {
/*     console.log("dispatching getContractInstance");
    this.$store.dispatch("getContractInstance"); */
  }
};
</script>

<style scoped>
